#start{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
    font-family: "rubber";
    background-image: url("Map_3.jpg");
    text-align: center;
    position: absolute;
    color: rgb(255, 255, 255);
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    font-size: 20px;
    margin-top: 30px;
    left: 0;
    right: 0;
    width: 1100px;
    height: 510px;
    border: 10px solid transparent;
    padding: 15px;
    //  background-color:black;
    padding: 2em;
    text-align: left;
    animation: animate_start 60s linear infinite ;
}

@keyframes animate_start {
0%{background-position: 0,0;}
100%{background-position: -1000px 0; }
}
  

.instructions:hover .instructions-modal {
